<template>
	<div class="home_content">
		<div>
			<home-plate-container contTitle="" pcCol="#f38849" pcBorderCol="#f38849">
				<home-article-cont>
					<h3 slot="articleTitle">DC</h3>
					<p slot="articleCont">DC</p>
					<img src="../assets/images/001.jpg" slot="articleImg">
					<div slot="articleAuthor">:<span></span></div>
				</home-article-cont>
				<home-article-cont>
					<h3 slot="articleTitle"> | </h3>
					<p slot="articleCont"></p>
					<img src="../assets/images/002.jpg" slot="articleImg">
					<div slot="articleAuthor">:<span></span></div>
				</home-article-cont>
			</home-plate-container>
			<home-plate-container contTitle="">
				<home-article-cont>
					<h3 slot="articleTitle">62-68</h3>
					<p slot="articleCont"> Z</p>
					<img src="../assets/images/003.jpg" slot="articleImg">
					<div slot="articleAuthor">:<span></span></div>
				</home-article-cont>
				<home-article-cont>
					<h3 slot="articleTitle"></h3>
					<p slot="articleCont">24+100+wifi+</p>
					<img src="../assets/images/004.jpg" slot="articleImg">
					<div slot="articleAuthor">:<span></span></div>
				</home-article-cont>
			</home-plate-container>
			<home-plate-container contTitle="">
				<home-article-cont v-for="item in arr"  :itemUrl="item.target.uri">
					<h3 slot="articleTitle">{{ item.title }}</h3>
					<p slot="articleCont">{{ item.target.desc }}</p>
					<img :src="item.target.author.avatar" slot="articleImg">
					<div slot="articleAuthor">:<span>{{ item.target.author.name }}</span></div>
				</home-article-cont>
			</home-plate-container>
		</div>
	</div>
</template>

<script>
import HomePlateContainer from './HomePlateContainer'
import HomeArticleCont from './HomeArticleCont'
import Wrap from './Wrap'

export default{
	components:{
		HomePlateContainer,
		Wrap,
		HomeArticleCont
	}
}
</script>

<style>
	.home_content{background-color: #f2f2f2;}
</style>



