import Vue from 'vue'
import Router from 'vue-router'
import Home from '../page/home/Home'
import Audio from '../page/audio/Audio'
import Group from '../page/group/Group'
import Mine from '../page/mine/Mine'
import Broadcast from '../page/broadcast/Broadcast'
import Film from '../components/Film'
import Book from '../components/Book'
import Tv from '../components/Tv'
import City from '../components/City'
import Music from '../components/Music'

// 

// vue-router
Vue.use(Router)

// 
export default new Router({
  routes: [
    {
      path: '/',		  // 
      name: '/',		  // 
      component:Home	  // 
    },
    {
    	path:'/home',
    	name:'Home',
    	component: Home
    },
    {
    	path:'/audio',
    	name:'Audio',
    	component: Audio
    },
    {
    	path:'/group',
    	name: 'Group',
    	component: Group
    },
    {
    	path:'/broadcast',
    	name: 'Broadcast',
    	component: Broadcast,
        children:[      // path roter-link to  /1/2
            {
                path:'/',   // path  / 
                component:Film
            },
            {
                path:'film',
                name:'Film',
                component:Film
            },
            {
                path:'book',
                name:'Book',
                component:Book
            },
            {
                path:'tv',
                name:'Tv',
                component:Tv
            },
            {
                path:'city',
                name:'City',
                component:City
            },
            {
                path:'music',
                name:'Music',
                component:Music
            }
        ]
    },
    {
    	path:'/mine',
    	name:'Mine',
    	component:Mine
    }

  ]
})



